export default {
    name: 'home',
    data(){
        return{
            routePath:[
                {
                    key:'homeModel',
                    name:[]
                },
                {
                    key:'userModel',
                    name:['用户列表']
                },
                {
                    key:'attachModel',
                    name:['院系管理']
                },
                {
                    key:'studentBasic',
                    name:['学生管理','基本信息']
                },
                {
                    key:'studentEmploy',
                    name:['学生管理','就业信息']
                },
                {
                    key:'counselorModel',
                    name:['辅导员管理']
                },
                {
                    key:'careerGuidModel',
                    name:['就业指导']
                },
                {
                    key:'presentModel',
                    name:['就业代表']
                }
            ]
        }
    },
    methods:{
        selectModel(index,path){
            this.routePath.map((item,key) =>{
                if(item.key == index){
                    this.$store.commit('changeBreadcrumbName', item.name);
                }
            })
            
        }
    },
    computed: {
      cachedViews() {
        // return this.$store.state.tagsView.cachedViews
      },
      key() {
        return this.$route.path
      },
      breadcrumbList(){
          return this.$store.state.breadcrumbName
      }
    }
  }